<template>
 <div class="center">
     <div class="center_breadcrumb"><crumb></crumb></div>
    <div class="center_content">
        <div class="task-situation_card">
            <ul>
                <li v-for="(vo, index) in card" :key="index" @click="taskDetails(vo)">
                    <div class="cart_lf">
                       <img :src="vo.imgUrl" />
                    </div>
                    <div class="cart_rt">
                        <span>{{vo.content}}</span>
                        <label>{{vo.title}}</label>
                    </div>
                    <div :class="{'arrows-right':index !==4, 'arrows-bottom': index ===2, 'arrows-left': index ===3}"></div>
                </li>
            </ul>
      </div>
    </div>
 </div>
</template>

<script>
import Vue from 'vue'
window.eventBus = new Vue()
export default {
 name: 'TaskSituation',
 components: {},
 data() {
 return {
   pageTitle:['教学任务管理', '任务完成情况'],
   card: [
        {
          imgUrl: require("@/assets/img/img-yiliuzhuanye.png"),
          path:'/studentside/objective',
          type:'taskSituation',
          content: "0条",
          title: "培养目标"
        },
        {
          imgUrl: require("@/assets/img/img-yitongguo.png"),
          path:'/studentside/requirement',
          type:'taskSituation',
          content: "0条-0条",
          title: "毕业要求-指标点"
        },
        {
          imgUrl: require("@/assets/img/img-yishenqing.png"),
          path:'/Construction/CourseConstructionHb',
          type:'taskSituation',
          content: "共0门已建设0门",   
          title: "课程大纲建设"
        },
        {
          imgUrl: require("@/assets/img/img-weirenzheng.png"),
          path:'/teachers/teachers1',
          type:'taskSituation',
          content: "共0份已导出0份",
          title: "成绩导入情况"
        },
        {
          imgUrl: require("@/assets/img/img-weirenzheng.png"),
          path:'/Construction/TestPaperConfigurationHb',
          type:'taskSituation',
          content: "共0门已配置0门",
          title: "试卷配置情况"
        }
      ],
      api:{
          task: scope => `/teacher/batch/task/statistics/${scope.specialId}/${scope.versionId}`
      }
 }
 },
 created(){
     window.eventBus.$emit('crossRegionChange', '/taskManagement/taskSituation')
 },
 mounted(){
     this.initData()
 },
 methods:{
     initData(){
         const postData={
             specialId:localStorage.getItem('specialId'),
             versionId:localStorage.getItem('versionId')
         }
         this.$get(this.api.task(postData)).then(res =>{

            this.card[0].content=`${res.goalSuCount}条` 
            this.card[1].content=`${res.claimCount}条-${res.pointCount}条` 
            this.card[2].content=`共${res.courseCount}门已建设${res.courseBuild}门` 
            this.card[3].content=`共${res.sumScore}份已导入${res.exportFinish}份` 
            this.card[4].content=`共${res.examNum}门已配置${res.examFinishNum}门` 
            
         })
     },
     taskDetails(vo){
       this.$router.push({
           path:vo.path,
           query:{
               type:vo.type
           }
       })
       window.eventBus.$emit('crossRegionChange', vo.path)
     }
 }
}
</script>

<style lang='scss' scoped>
.task-situation_card{
    height:100%;
    margin:30px 0;
    clear: both;
    position: relative;
    box-sizing: border-box;
    overflow: auto;
    ul{
        height: 100%;
        display:flex;
        flex-wrap: wrap;    
        li{
            height: 200px;
            width: 22vw;
            margin-right: 5vw;
            border-radius:10px;
            display:flex;
            justify-content:center;
            align-items:center;
            margin-bottom: 5vw;
            cursor:pointer;
            position:relative;
            &:nth-of-type(1){
                background:linear-gradient(90deg,rgba(255,224,152,1) 0%,rgba(255,163,81,1) 100%);
            }
            &:nth-of-type(2){
                background:linear-gradient(90deg,rgba(179,220,255,1) 0%,rgba(90,179,255,1) 100%);
            }
            &:nth-of-type(3){
                background:linear-gradient(90deg,rgba(142,233,202,1) 0%,rgba(3,177,135,1) 100%);
                margin-right:0;
            }
            &:nth-of-type(4){
                background:linear-gradient(90deg,rgba(218,213,255,1) 0%,rgba(162,150,255,1) 100%);
                margin-left: 26.8vw;
            }
            &:nth-of-type(5){
                background:linear-gradient(90deg,rgba(218,213,255,.9) 0%,rgba(162,150,255,.9) 100%);
                margin-right:0;
            }
            .cart_lf{
                height:100%;
                width:20%;
                display:flex;
                justify-content:center;
                align-items:center;
                img{
                    width:51px;
                    height:60px;
                }
            }
            .cart_rt{
                height:100%;
                width:80%;
                display:flex;
                flex-direction:column;
                justify-content:center;
                align-items:center;
                span{
                    font-size:1.8vw;
                    font-weight:bold;
                    color:#fff;
                }
                label{
                    font-size:18px;
                    font-weight:bold;
                    color:#fff;
                    margin-top: 10px;
                }
            }
        }
        .arrows-right{
             width:30px;
             height:10px;
             position:absolute;
             right: -3.6vw;
             cursor:default;
             &:before{
                content:'';
                display:block;
                width:20px;
                height:2px;
                border-bottom:2px dotted  #03B187;
             }
             &:after{
                content: '';
                display: block;
                width: 10px;
                height: 10px;
                border-top: 2px solid #03B187;
                border-right: 2px solid #03B187;
                transform: rotate(45deg) translateY(-17px) translateX(8px);
             }
        }
        .arrows-bottom{
             transform:rotate(90deg);
             right:180px;
             bottom:-2.6vw;
        }
        .arrows-left{
             transform:rotate(180deg);
        }
    }
}
</style>



